.wrapper{
    text-align: center;
    width: 60%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.create-folder{
    padding: 10px;
    background-color: #272727;
    border-radius: 12px;
    width: 200px;
    margin: 10% 0%;
    cursor: pointer;
}




.profile-info-buttons button, .edit-profile-btn{
    color: white;
    border: 1px solid white;
    padding: 2px 0px;
    width: 4rem;
    border-radius: 6px;
    font-size: 12px;
    margin-top: 1%;
    transition: .25s;
}

.profile-info-buttons{
    margin-top: 4%;
}


.bio-interests-texts{
    font-size: 14px;
    color: rgb(177, 177, 177);
}



.add-btn, .submit-btn, .edit-btn, .update-btn{
    background-color: #7869E6;
    padding: 2px 10px;
    border-radius: 6px;
    transition: .2s;
    margin-left: .5rem;
    font-size: 14px;
    font-weight: 500;
}

.add-btn:hover, .submit-btn:hover, .edit-btn:hover, .update-btn:hover{
    background-color: #6d5bf8;
    padding: 3px 11px;
    letter-spacing: .4px;
    
    
}

.add-btn:active, .submit-btn:active, .edit-btn:active, .update-btn:active{
    background-color: #9284f6;
}

.cancel-btn{
    background-color: none;
    padding: 1px 9px;
    border: 1px solid white;
    border-radius: 6px;
    transition: .2s;
    margin-left: .5rem;
    font-size: 14px;
    font-weight: 500;
}
.cancel-btn:hover{
    padding: 2px 10px;
    letter-spacing: .4px;
}





/***************Liked Tools*****************/
.folder-item{
    background-color: #0D0C12 !important;
    color: #fff;
    width: 100%;
}