.tools-wrapper{
    min-width: 100%;
    min-height: 46.7vh;
    background-color: #000;
    color: #fff;
    text-align: center;
    align-items: center;
}


.description{
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical; 
    overflow: hidden;
    width: 370px;
    text-align: center;
}

:root {
    /* GENERAL FONT */
    --font1: "Titillium Web", sans-serif;
    --font2: 'Roboto', sans-serif;
    /* GENERAL COLOR */
    --bg-col-1: #5c6bc0;
    --bg-col-2: #dc3545;
    --bg-col-3: #fff;
    --bg-col-4: #5c6bc0;
    --tx-color-1: #fff;
    --tx-color-2: #000;
    --tx-color-3: #5c6bc0;
    // FOCUS START
    /* READ MORE COLOR */
    --tx-rm-color-1: #fff;
    --bg-rm-color-1: #fff;
    /* TAG BOTTOM-RIGHT COLOR */
    /* FOOTER CARD COLOR */
    --bg-ft-col-1: #5c6bc0;
    --tx-ft-color-1: #fff;   
    // focus section
    --tx-focus-section-title: #212529;
    --bg-focus-section: aliceblue;
    --bg-focus-card-overlay: rgba(0, 89, 179, 0.5);
    --tx-focus-hover: #fff;
    --tx-focus-font-weight: 400;
    --tx-focus-tag-color-1: #fff;
    --bg-focus-tag-color-1: #5c6bc0;
    --bg-focus-cat: #5c6bc0;
    --tx-focus-cat: #fff;
    --bg-focus-footer: #5c6bc0;
    --tx-focus-footer: #fff;
    --bg-focus-card: #fff;
    --tx-focus-card: #000;
    //button arrow & archive
    --tx-button: #fff;
    --bg-button: #5c6bc0;
}
/*start dark mode css*/
.dark-mode {
    /* GENERAL COLOR */
    --bg-col-1: #121212;
    --tx-color-1: #fff;
    //button arrow & archive
    --tx-button: #fff;
    --bg-button: #272727;
    --bg-button-border: #272727;
    --bg-button-hover: #fff;
    --tx-button-hover: #272727;
    --bg-button-focus: #0f0f0f40;
    //focus-section
    --tx-focus-section-title: #fff;
    --bg-focus-section: #121212;
    --bg-focus-card-overlay: rgba(0, 0, 0, 0.5);
    --tx-focus-hover: #fff;
    --bg-focus-card: #272727;
    --tx-focus-card: #fff;
    --tx-focus-font-weight: 100;
    --tx-focus-tag-color-1: #fff;
    --bg-focus-tag-color-1: #272727;
    --tx-focus-cat: #272727;
    --bg-focus-cat: #fff;
    --tx-focus-tag-a: #fff;
    --bg-focus-footer: #272727;
    --tx-focus-footer: #fff;
    --focus-border-card: 0;
}
/* end dark mode css */
.btn-archive {
    color: var(--tx-button);
    background: var(--bg-button);
    border-color: var(--bg-button-border);

    -webkit-transition: all .3s linear 0s;
    transition: all .3s linear 0s;

    &:hover {
        color: var(--tx-button-hover);
        background-color: var(--bg-button-hover);
        border-color: var(--bg-button-hover);
    }

    &:focus {
        -webkit-box-shadow: 0 0 0 0.25rem var(--bg-button-focus);
        box-shadow: 0 0 0 0.25rem var(--bg-button-focus);
    }
}

.focus-section {
    background: var(--bg-focus-section);
    font-family: var(--font1);
    -webkit-transition: all .3s linear 0s;
    transition: all .3s linear 0s;

    .title-section {
        font-weight: 700;
        text-transform: uppercase;
        color: var(--tx-focus-section-title);
    }

    .card {
        font-size: 1rem;
        border: var(--focus-border-card);

        .card-body {
            background: var(--bg-focus-card);
            color: var(--tx-focus-card);

            .card-text {
                font-weight: var(--tx-focus-font-weight);
            }
        }

        .image-overlay .delete-btn{
            position: absolute;
            top: 20px;
            right:20px;
            background-position: center;
            text-align: center;
            cursor: pointer;
        }

        .card-image {
            position: relative;
            text-align: center;
            .card-img-top{
                margin-left: auto;
                margin-right: auto;
                width: 50%;
            }
            .image-overlay {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                height: 100%;
                width: 100%;
                opacity: 0;
                transition: 0.5s ease;
                background: var(--bg-focus-card-overlay);
            }

            &:hover .image-overlay {
                opacity: 1;
            }
        }

        .text-img-over {
            position: relative;


            .overlay {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                height: 100%;
                width: 100%;
                opacity: 0;
                transition: 0.5s ease;
                background: var(--bg-focus-card-overlay);
            }

            &:hover .overlay {
                opacity: 1;
            }
        }

        .social-hover {
            color: var(--tx-focus-hover);
            font-size: 30px;
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            text-align: center;


            .fa-share-alt {
                color: var(--tx-focus-hover);

                &:hover {
                    color: var(--tx-color-1);
                }
            }


        }

        .hover-text {
            position: relative;
            text-align: center;
            color: var(--tx-focus-hover);
        }

        .bottom-right-tag {
            position: absolute;
            bottom: 0;
            right: 0;
            background: var(--bg-focus-tag-color-1);
            padding: 5px 10px 5px 10px;
            border-top-left-radius: calc(.25rem - 1px);
            font-size: 0.8rem;
            font-weight: 200;
            color: var(--tx-focus-tag-color-1);
            z-index: 1;

            a {
                color: var(--tx-focus-tag-color-1);
                text-decoration: none;
            }

            a:hover {
                color: var(--tx-focus-tag-color-1);
                text-decoration: underline;
            }

        }



        .sub-cat {
            font-weight: 600;
            font-size: 1rem;

            .bg-category {
                background: var(--bg-focus-cat);
                color: var(--tx-focus-cat);
            }

            a {
                color: var(--tx-focus-tag-a);
            }
        }



        .card-title {
            font-family: var(--font1);
            font-weight: 600;
            font-size: 1.3rem;
            /* height: 73px;
  overflow: hidden; */
        }

        .card-footer {
            background: var(--bg-focus-footer);
            color: var(--tx-focus-footer);

            .card-footer__info {
                font-size: 1rem;
                font-weight: 300;
                position: relative;
            }

        }


        .read-more {
            position: absolute;
            right: 0;
            font-weight: 600;
        }

        .read-more-1 {
            text-decoration: none;
            position: relative;
            color: var(--tx-focus-footer);
        }

        .read-more-1::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 2px;
            background: var(--bg-rm-color-1);
            transform: scaleX(0);
            transform-origin: left;
            transition: transform 250ms ease-in;
        }

        .read-more-1:hover {
            color: var(--tx-focus-footer);
        }

        .read-more-1:hover::after {
            transform: scaleX(1);
            transform-origin: right;
            color: var(--tx-focus-footer);
        }

        .read-more-2 {
            text-decoration: none;
            position: relative;
            margin-right: 15px;
            color: var(--tx-focus-footer);
        }

        .read-more-2::after {
            content: "";
            /* You should use \ and not /*/
            font-family: "Font Awesome 5 Free";
            /* This is the correct font-family*/
            position: absolute;
            right: -18px;
            bottom: 0;
            color: var(--tx-focus-footer);
        }

        .read-more-2:hover {
            color: var(--tx-focus-footer);
        }

        .read-more-2:hover::after {
            transform: translate(3px, 0);
            transition: all 0.1s ease-in;
            color: var(--tx-focus-footer);
        }

        .read-more-3 {
            text-decoration: none;
            position: relative;
            color: var(--tx-focus-footer);
        }

        .read-more-3:hover {
            color: var(--tx-focus-footer);
            text-decoration: underline;
        }


    }
}